.App {
  text-align: center;
}

.App-header {
  background-color: #333333;
  height: 50px;
  padding: 20px;
  color: #CFCDCD;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.topology {
  height: 80%;
}

.navbar {
  padding-top: 0px;
  padding-bottom:0px;
  padding-left: 50px;
  margin-bottom: 50px;
}

.login {
  font-size: .80em;
  margin-right:50px;
}
.logSearch {
  padding-top: 0px;
  margin-bottom: 20px;
}

#devnetLogo {
  margin-left: 15px;
  font-size: 30px;
  font-weight: bold;
  padding-top: -10px;
  padding-right: 15px;
}

#navList {
  padding-top:13px;
}

content {
  margin-top: 120px;
  margin-left: 0px;
  display: block;
  margin-top: -10px;
  background: transparent;
}

@-webkit-keyframes buttonTransition {
  from {
      -webkit-transform: scale(0.1);
              transform: scale(0.1);
  }
  to {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes buttonTransition {
  from {
      -webkit-transform: scale(0.1);
              transform: scale(0.1);
  }
  to {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}


footer {
  background: #cccccc;
  height: 200px;
}

svg {
  max-width: 100%;
  max-height: 100%;
}






